<template>

<div class="card">
  <header class="card-header">
    <p class="card-header-title">
      Disk Usage:
    </p>
  </header>
  <div class="card-content">
    <div class="content">
        <p>Total: {{trans(this.resp.diskSpace)}}</p>
        <p>Free: {{trans(this.resp.diskFree)}}</p>
        <p>Free: {{diskFreeRate}}%</p>
        <progress class="progress" :value="100-diskFreeRate" max="100"></progress>
    </div>
  </div>
</div>

</template>

<script>
    export default {
        props:['resp'],

        computed: {
            diskFreeRate : function() {
                return Math.floor(100 *this.resp.diskFree / this.resp.diskSpace);
            },
        },

        methods:{
            trans : function($bytes) {
                let $symbols, $exp
                $symbols = ['B', 'KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
                $exp = Math.floor(Math.log($bytes)/Math.log(1024));
                return Math.round($bytes/Math.pow(1024, Math.floor($exp))) + $symbols[$exp];
            }
        },

        created() {
            console.log('[+]DiskSpaceCard Created')
        }

    }

</script>